html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

* {
  margin: 0;
}

body {
  background-color: #f4d04e;
}

/* Fonts */
@font-face {
  font-family: Figtree;
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/static/Figtree-Medium.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: Figtree;
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/static/Figtree-ExtraBold.ttf") format("truetype");
  font-display: swap;
}
body {
  font-family: Figtree, Inter, "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.visually-hidden {
  position: absolute !important;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  overflow: hidden;
}

.main {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f4d04e;
  padding: 0 24px;
}

.card {
  background-color: #fff;
  max-width: 384px;
  border: 1px solid #111;
  border-radius: 20px;
  padding: 24px;
  box-shadow: 8px 8px 0 0 rgb(0, 0, 0);
}
.card__figure {
  margin-bottom: 24px;
}
.card__image {
  width: 100%;
  height: 200px;
  border-radius: 10px;
  object-fit: cover;
  display: block;
}
.card__content {
  display: flex;
  flex-direction: column;
  gap: 12px;
  color: #111;
}
.card__category {
  padding: 4px 12px;
  background-color: #f4d04e;
  max-width: fit-content;
  border-radius: 4px;
  font-size: clamp(12px, 11.2957746479px + 0.1877934272vw, 14px);
  font-weight: 800;
  line-height: 150%;
  letter-spacing: 0;
}
.card__date {
  font-size: clamp(12px, 11.2957746479px + 0.1877934272vw, 14px);
  font-weight: 500;
  line-height: 150%;
  letter-spacing: 0;
}
.card__title {
  transition: color 0.2s ease-in-out;
  font-size: clamp(20px, 18.5915492958px + 0.3755868545vw, 24px);
  font-weight: 800;
  line-height: 150%;
  letter-spacing: 0;
}
.card__title:hover {
  color: #f4d04e;
  cursor: pointer;
}
.card__description {
  color: #6b6b6b;
  font-size: clamp(14px, 13.2957746479px + 0.1877934272vw, 16px);
  font-weight: 500;
  line-height: 150%;
  letter-spacing: 0;
}
.card__footer {
  display: flex;
  gap: 12px;
  align-items: center;
  margin-top: 12px;
  font-size: clamp(12px, 11.2957746479px + 0.1877934272vw, 14px);
  font-weight: 800;
  line-height: 150%;
  letter-spacing: 0;
}